import React from 'react';
import classes from "./Users.module.css";
import photoPlaceholder from "../../../assetst/placeholder.png";
import {NavLink} from "react-router-dom";
import Paginator from "../../common/paginator/Paginator";

const User = ({user, getFollow, getUnfollow}) => {
  return (
    <div>
        <span>
          <div>
            <NavLink to={'/profile/' + user.id}>
            <img className={classes.photo}
                 src={user.photos.small != null ? user.photos.small :
                   photoPlaceholder}
                 alt="photoUrl"/>
            </NavLink>
          </div>
          <div>
            {user.followed ? <button
                onClick={() => {
                  getUnfollow(user.id)
                }}>Unfollow</button> :
              <button
                onClick={() => {
                  getFollow(user.id)
                }}>Follow</button>}
          </div>
        </span>
      <span>
          <span>
            <div>{user.name}</div>
            <div>{user.status}</div>
          </span>
        </span>
    </div>
  );
};

export default User;