import React, {useState} from 'react';
import classes from "./Paginator.module.css";

const Paginator = ({
                     totalUsersCount,
                     pageSize,
                     currentPage,
                     onPageChanged,
                     portionSize = 10,
                   }) => {
  let pagesCount = Math.ceil(totalUsersCount / pageSize);
  const pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  /// come back to this useState, why it changes its state after reloading?
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPageNumber = portionNumber * portionSize;
  return (
    <div className={classes.paginatorBox}>
      <div className={classes.pageNumbers}>
        {portionNumber > 1 && <button
          onClick={() => {setPortionNumber(portionNumber - 1)}}>PREV</button>}
        {pages
          .filter(
            p => (p >= leftPortionPageNumber && p <= rightPortionPageNumber))
          .map(p => {
            return <span
              className={currentPage === p && classes.selectedPage}
              onClick={() => {onPageChanged(p)}}>{p}</span>
          })}
        {portionCount > portionNumber && <button
          onClick={() => { setPortionNumber(portionNumber + 1)}}>NEXT</button>}
      </div>
    </div>
  )
};

export default Paginator;