import {LoginAPI, userAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'tuanneli/SET_USER_DATA';

const initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
}

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.payload,
      }
    default:
      return state;
  }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  payload: {userId, email, login, isAuth},
});

export const getAuthThunkCreator = () => async (dispatch) => {
  const response = await userAPI.getAuth()
  if (response.resultCode === 0) {
    let {id, login, email} = response.data;
    dispatch(setAuthUserData(id, email, login, true));
  }
}

export const loginThunkCreator = (login, password,
                                  rememberMe) => async (dispatch) => {
  console.log("Im here")
  const response = await LoginAPI.postLoginData(login, password, rememberMe)
  if (response.data.resultCode === 0) {
    dispatch(getAuthThunkCreator())
  } else {
    let messages = response.data.messages.length > 0 ?
      response.data.messages[0] : "Some error!"
    dispatch(stopSubmit("login", {_error: messages}));
  }
}

export const logoutThunkCreator = () => async (dispatch) => {
  const response = await LoginAPI.deleteLogoutData()
  if (response.data.resultCode === 0) {
    dispatch(setAuthUserData(null, null, null, false))
  } else {
    alert("Wasn't able to logout")
  }
}

export default authReducer;


